.weather {
  /* Отключает стрелочки у input type=number */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }

  .chart {
    margin-top: 20px;
    margin-bottom: 30px;
  }

  .chart tspan {
    fill: $color-02;
  }
}

// TODO:  ⇩ ⇩ ⇩ погода от MSN

.weather {
  width: 1260px;
  margin-right: auto;
  margin-left: auto;

  .weather__search {
    padding: 16px;
    width: 100%;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    @include card-style;

    .search-group {
      display: flex;
      align-items: center;
    }
  }

  .search__input-group {
    right: 0px;
    padding-right: 10px;
    border: 1px solid rgb-a($color-02, 50%);
    border-radius: 12px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    width: 281px;
    box-shadow: none;
    color: unset;
    backdrop-filter: blur(60px);
    background: rgb-a($color-09, 50%);
    margin-left: 0px !important;
    flex-wrap: wrap;
    padding: 0;

    & input {
      appearance: none;
      width: calc(100% - 80px);
      height: 40px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      font-family: inherit;
      box-sizing: border-box;
      margin: 0;
      transition: all 0.2s ease-in-out;
      font-size: 14px;
      line-height: 20px;
      font-weight: 400;
      height: 38px;
      outline: none;
      box-shadow: none;
      border-top: none;
      border-bottom: none;
      border-radius: 0px;
      border: 0px;
      background: none !important;
      padding: 0 10px;
      color: $color-02;
    }

    .input-group__btn-location {
      margin-left: 10px;
    }

    & button {
      border: none;
      outline: none;
      cursor: pointer;
      padding: 0px;
      background: none;
      height: 12px;
      width: 15px;
      margin-right: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $color-03;
    }
  }

  .search__card-city {
    margin-left: 8px;
    box-sizing: border-box;
    flex-shrink: 0;
    height: 40px;

    .card-city {
      height: 40px;
      padding: 9px 16px 9px 16px;
      gap: 10px;
      border-radius: 12px;
      border: 1px solid rgb-a($color-02, 50%);
      box-sizing: border-box;
      background: rgb-a($color-09, 51%);
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 16px;
      cursor: pointer;
      color: $color-02;
    }

    .card-city__image {
      width: 22px;
    }
  }

  .weathet__block-day {
    display: flex;
    justify-content: space-between;
  }

  .weather__current-day {
    width: 620px;
    height: 270px;
    padding: 16px 0px 0px;
    margin-bottom: 20px;
    @include card-style;

    .current-day__label {
      padding: 0px 16px;

      .label__title {
        font-size: 14px;
        line-height: 22px;
        font-weight: 600;
      }

      .label__time {
        font-size: 12px;
        line-height: 14px;
      }
    }

    .current-day__summary {
      padding: 0px 0px 0px 16px;

      .summary__overview {
        display: flex;
        align-items: center;

        .overview__image {
          width: 100px;
          height: 100px;
        }

        .overview__temperatura {
          margin-left: 8px;
          font-size: 64px;
          line-height: 62px;
          display: flex;
          align-items: flex-start;
        }

        .overview__temperatura span {
          height: 100%;
          font-size: 34px;
          display: flex;
          line-height: 1.5;
          align-items: flex-start;
        }

        .overview__feedback {
          margin: 0px 0px 0px 32px;

          .feedback__condition {
            font-size: 18px;
            line-height: 24px;
            font-weight: 600;
          }
          .feedback__temperature {
            font-size: 14px;
            line-height: 16px;
            opacity: 0.8;
            margin-right: 12px;
          }
        }
      }

      .summary__description {
        height: 65px;
        display: flex;
        align-items: center;
        margin-bottom: 9px;
        font-size: 16px;
        line-height: 22px;
        margin: 0;
      }
    }
    .weather__detail-line {
      padding-left: 16px;
      margin-bottom: 16px;
      padding-right: 16px;
      display: flex;
      justify-content: space-between;

      .detail-line__title {
        font-size: 12px;
        font-weight: normal;
        line-height: 16px;
      }

      .detail-line__value {
        font-size: 16px;
        line-height: 22px;
        font-weight: normal;
      }

      .detail-line__value--wind {
        display: flex;
        align-items: center;

        & svg {
          fill: $color-02;
        }
      }
    }
  }

  .weather__month {
    width: 1260px;
    padding: 12px 16px 1px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    @include card-style;

    .month__day-name {
      display: flex;
      justify-content: space-between;
      padding: 0;
      margin: 0;
      width: 100%;

      .day-name__item {
        list-style: none;
        width: calc((1260px - 32px) / 7);
        padding: 0;
        margin-bottom: 15px;
      }
    }

    .month__block-day {
      margin: 0;
      padding: 0;
      display: flex;
      flex-wrap: wrap;

      .block-day__card {
        list-style: none;
        width: calc((1260px - 32px) / 7);
        height: 115px;
        padding-right: 5px;
        position: relative;

        .card__block {
          border: 1px solid transparent;
          width: 100%;
          height: 85px;
          padding: 0;
          margin: 0;
          border-radius: 6px;
          position: relative;

          &.card__block--on::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            border-radius: 15px;
            background: rgb-a($color-07, 50%);
            top: 0;
            left: 0;
          }

          &.card__block--current {
            border: 1px $color-03 solid;
          }

          .block__title {
            margin: 4px 0px 0px 8px;
            font-size: 14px;
          }

          .block__description {
            display: flex;
            padding: 0px 16px;

            .description__text {
              display: flex;
              flex-direction: column;
              margin-left: 6px;

              p {
                font-size: 18px;
                font-weight: 600;
                line-height: 24px;
              }
            }
          }
        }

        .card__block:hover {
          border: 1px $color-03 solid;
          @include animation-slider(border);
        }

        .card__detail {
          position: absolute;
          bottom: 50%;
          left: 50%;
          width: 80%;
          z-index: 1;
          background: $color-08;
          display: block;
          border-radius: 15px;
          padding: 10px;
          fill: $color-03;
          opacity: 0;
          pointer-events: none;
          @include animation-slider(all);

          .detail__item {
            display: flex;
            align-items: center;
            padding: 3px;

            svg {
              margin-right: 6px;
            }
          }
        }

        .card__block:hover + .card__detail,
        .card__detail:hover {
          opacity: 1;
        }

        .card__detail:hover ~ .card__block:hover {
          border: 1px $color-03 solid;
        }
      }
    }
  }

  // !-----------------------------------------------------
  // !-----WhetrMonthMobile--------------------------------
  // !-----------------------------------------------------

  .weather10days {
    padding: 0px 6px 0;

    background: $color-07;
    border-radius: 15px;

    .weather10days__title {
      font-size: 16px;
      line-height: 21px;
      font-weight: normal;
      box-sizing: border-box;
      margin: 0px;
      padding: 8px 16px;
      border-bottom: 1px solid rgb-a($color-03, 12%);
      text-transform: capitalize;
      position: relative;
      color: $color-02;
      display: flex;
      justify-content: space-between;

      .title__text {
        flex-shrink: 0;
        margin-right: 10px;
      }
      .title__btn {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: right;
        padding-right: 12px;
        color: rgb-a($color-02, 60%);
        background: none;
        border: none;
        text-transform: capitalize;
        text-decoration: none;
        font-size: 14px;
        font-weight: normal;
        outline: none;
        text-shadow: 1px 1px 2px rgb(0 0 0 / 20%);
      }
    }
    .weather10days__days {
      width: 100%;
      overflow: hidden;
      height: 157px;

      .days__daily-carousel {
        overflow: auto;

        .daily-carousel__carousel {
          z-index: 1;
          margin-bottom: 30px;
          width: calc((100vw * 2) - 64px);
          position: relative;

          & div {
            position: relative;
            overflow: hidden;
          }

          .card-container {
            height: auto;
            transform: translate3d(0px, 0px, 0px);
            transition: all;
            white-space: nowrap;
            display: flex;
            padding: 0;
            margin: 0;

            .container {
              display: inline-block;
              margin-right: 7px;
              width: calc((100vw - 64px) / 5);
              box-sizing: border-box;
              padding: 0px 0px 16px;
              // margin: 0;
              // padding: 0;
              text-align: center;

              .day-card {
                width: 100%;
                height: auto;
                background: transparent;
                outline: none;
                border: none;
                width: inherit;
                font-family: inherit;
                position: relative;
                display: flex;
                justify-content: space-between;
                align-items: center;
                cursor: pointer;
                height: 155px;
                text-align: center;
                background: rgb-a($color-02, 3%);
                border-radius: 15px;
                // -webkit-tap-highlight-color: transparent;
                padding: 0;
                margin: 0;
                padding: 0;
                text-align: center;

                .set-focus {
                  margin: 0;
                  padding: 0;
                  text-align: center;
                  width: inherit;
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  user-select: none;

                  .main-part {
                    display: block;
                    margin: 0;
                    padding: 0;
                    text-align: center;
                    width: 100%;

                    .header {
                      font-size: 12px;
                      line-height: 16px;
                      color: $color-02;
                      opacity: 1;
                      margin-bottom: 12px;
                      text-overflow: ellipsis;
                      overflow: hidden;
                      white-space: nowrap;
                    }

                    .icon-temp-part {
                      margin: 0;
                      padding: 0;
                      text-align: center;
                      width: 100%;
                      box-sizing: border-box;

                      .icon-temp-part__image {
                        // width: 36px;
                        height: 36px;
                      }

                      .icon-temp-part__tempmax {
                        font-size: 14px;
                        line-height: 22px;
                        margin-top: 12px;
                        color: $color-02;
                        opacity: 1;
                      }

                      .icon-temp-part__tempmin {
                        font-size: 14px;
                        line-height: 22px;
                        margin-top: 2px;
                        color: $color-02;
                      }

                      .icon-temp-part__precip {
                        font-size: 14px;
                        line-height: 22px;
                        margin-top: 2px;
                        color: $color-02;

                        .icon-temp-part__precip-icon {
                          font-size: 14px;
                          line-height: 22px;
                          margin-top: 2px;
                          margin-right: 6px;
                          color: $color-02;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  // *------------modal------------------------------------

  .modal {
    position: absolute;
    opacity: 0;
    pointer-events: none;
    width: calc(100% - 20px);
    // height: 100%;
    background: $color-07;
    z-index: -5;
    top: 0;
    // margin-top: 45px;
    border-radius: 15px;
    @include card-style;

    &.open {
      opacity: 1;
      pointer-events: all;
      z-index: 100;
    }

    .modal__header {
      display: flex;
      justify-content: flex-end;
      margin: 16px;

      .btn-close {
        @include button;
      }
    }

    .month-calendar {
      position: relative;
      display: block;
      width: 100%;
      border-radius: 15px;
      background: trasparent;
      backdrop-filter: blur(25px);
      // -webkit-backdrop-filter: blur(25px);
      // padding: 0 12px;
      box-sizing: border-box;

      .month-calendar__table-container {
        position: relative;
        width: 100%;
        padding: 16px 12px;
        box-sizing: border-box;
        background: rgb-a($color-02, 3%);
        backdrop-filter: blur(25px);
        border-radius: 8px;

        .table-container__table-content {
          position: relative;
          width: 100%;
          box-sizing: border-box;

          .table-content__content-items {
            position: relative;
            margin: 0;
            padding: 0;
            display: flex;
            flex-wrap: wrap;

            .content-item {
              flex-basis: 25%;
              box-sizing: border-box;
              list-style: none;
              list-style-type: none;
              width: 70px;
              height: auto;
              padding: 6px;
              // color: #444;
              margin-bottom: 0;
              display: flex;
              justify-content: center;
              border-bottom: 1px solid rgb-a($color-03, 15%);

              & div {
                border-radius: 12px;
                background: transparent;
                border-top: 0.5px solid rgb-a($color-03, 15%);
                box-shadow: none;
                position: relative;
                border: none;
                max-width: 80px;
                width: 100%;
                height: 80px;
                padding: 0;
                margin: 0;
                display: inline-block;

                &.activ {
                  border: 0.5px solid rgb-a($color-03, 15%);
                  background: rgb-a($color-03, 5%);
                }

                .calendar-table-day {
                  border-radius: 12px 12px 0 0;
                  width: 100%;
                  height: 30px;
                  background: none;
                  display: flex;
                  padding: 0 8px;
                  box-sizing: border-box;
                  justify-content: space-between;
                  align-items: center;

                  & span:first-child {
                    font-size: 18px;
                    font-weight: 600;
                    color: $color-02;
                  }
                  & span:last-child {
                    font-size: 12px;
                    color: rgb-a($color-02, 40%);
                  }
                }

                .calendar-table-day-forecast {
                  height: 50px;
                  box-sizing: border-box;
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  padding: 9px 8px;

                  .calendar-table-day-forecast__temp {
                    font-size: 12px;
                    height: 32px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    text-align: right;
                    & span:first-child {
                      color: rgb-a($color-02, 80%);
                      font-weight: 600;
                    }
                    & span:last-child {
                      color: rgb-a($color-02, 80%);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  // *-----------------------------------------------------

  // !-----------------------------------------------------
  // !-----------------------------------------------------
  // !-----------------------------------------------------
  //
  // !-----------------------------------------------------
  // !-----WeatherSunMoonMobile--------------------------------
  // !-----------------------------------------------------

  .sun-and-moon {
    margin: 16px 0px 16px;
    // background-color: rgba(255, 255, 255, 0.05);
    background: $color-07;
    border-radius: 4px;
    @include card-style;

    h2 {
      font-size: 16px;
      line-height: 21px;
      font-weight: normal;
      box-sizing: border-box;
      margin: 0px;
      padding: 8px 16px;
      border-bottom: 1px solid rgb-a($color-03, 12%);
    }
    div {
      width: 100%;
      div {
        .datail-container {
          font-size: 12px;
          line-height: 16px;
          font-weight: normal;
          color: $color-02;
          position: relative;
          padding: 16px;
          box-sizing: border-box;
          display: block;

          .detail-items {
            display: block;

            .detail-items__flex {
              display: flex;
              flex-direction: row;
              justify-content: space-between;

              .detail-items__grid {
                // width: calc(50% - 8px);
                max-width: 154px;
                display: flex;
                justify-content: center;
                align-items: center;

                & span {
                  height: 32px;
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  justify-content: center;
                  flex-shrink: 0;
                  flex-grow: 0;
                  margin-right: 16px;
                }

                // & *:first-child {
                //   margin-right: 16px;
                // }

                .label {
                  width: unset;
                  max-width: 140px;
                  word-break: break-word;
                  font-size: 10px;
                  line-height: 16px;
                  font-weight: 400;
                  opacity: 0.8;
                  white-space: normal;
                  // word-break: break-word;
                  position: relative;
                }
                .value {
                  font-size: 12px;
                  line-height: 16px;
                  font-weight: 600;
                  opacity: 1;
                  white-space: normal;
                  word-break: break-word;
                  position: relative;
                }
              }
            }
            .datail-items__day-night {
              display: flex;
              flex-direction: row;
              justify-content: space-between;

              .day-night__grid {
                display: block;
                text-align: center;
                // width: calc(50% - 8px);
                max-width: 154px;
                margin-top: 20px;

                div {
                  position: relative;

                  .day-icon {
                    width: 100%;
                    height: 55px;
                    overflow: hidden;
                    &.night-icon {
                      // transform: rotate(180deg);
                    }

                    &__svg {
                      width: 113px;
                      height: 113px;
                      max-width: unset;
                      max-height: unset;
                    }
                  }

                  .day-value {
                    position: absolute;
                    top: 80%;
                    left: 50%;
                    transform: translate(-50%, -20%);
                    width: 103px;
                    word-break: break-word;

                    &.night-value {
                      // top: 20%;
                    }
                  }
                }

                .sun-moon-label {
                  display: flex;
                  justify-content: space-between;
                  margin-top: 11px;

                  .sun-moon-label__text {
                    display: flex;
                    flex-direction: column;
                    max-width: 35px;

                    &.sun-moon-label__text--moon {
                      max-width: 65px;
                    }

                    & p:first-child {
                      white-space: nowrap;
                      text-overflow: ellipsis;
                      // max-width: 75px;
                      overflow: hidden;
                      font-size: 10px;
                      line-height: 16px;
                      font-weight: 400;
                      opacity: 0.8;
                      white-space: normal;
                      word-break: break-word;
                      position: relative;
                      display: block;
                    }

                    & p:last-child {
                      font-size: 12px;
                      line-height: 16px;
                      font-weight: 600;
                      opacity: 1;
                      white-space: normal;
                      word-break: break-word;
                      position: relative;
                      display: block;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  // !-----------------------------------------------------
  // !-----------------------------------------------------
  // !-----------------------------------------------------

  @media screen and (max-width: 720px) {
    .weather__current-day {
      width: 100%;
      margin-top: 40px;
      background: rgb-a($color-07, 70%);
      padding: 16px;
      border-radius: 15px;
      margin-top: 0;

      .current-day__label {
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .label__title {
          font-size: 12px;
        }

        .label__time {
          font-size: 12px;
        }
      }

      .current-day__summary {
        padding: 0;

        .summary__overview {
          .overview__image {
            width: 80px;
            height: 80px;
          }

          .overview__temperatura {
            font-size: 56px;
          }

          .overview__temperatura span {
            font-size: 30px;
          }

          .overview__feedback {
            .feedback__condition {
              font-size: 14px;
            }

            .feedback__temperature {
              font-size: 12px;
            }
          }
        }

        .summary__description {
          font-size: 14px;
        }
      }

      .weather__detail-line {
        padding: 0;
        // .detail-line__title {
        // }
        // .detail-line__value {
        // }
        // .detail-line__value--wind {
        //   & svg {
        //   }
        // }
      }

      .weather__detail-line div:nth-child(1),
      .weather__detail-line div:nth-child(6) {
        display: none;
      }
    }

    .weather__air-quality {
      display: none;
    }
  }
}

// TODO: ⇧ ⇧ ⇧ погода от MSN

@media screen and (max-width: 720px) {
  .weather {
    width: 100%;

    .dayCard {
      width: 100%;
    }

    .weather__day {
      flex-direction: column;
    }

    .weather__day > div {
      width: 100%;
    }

    .blockDayRight {
      display: flex;
      justify-content: space-around;
      width: calc(100% - 10px);
    }
  }
}
